:root{
  --background-color: #d4d4d4;
  --fill-color: rgba(255, 255, 255, .4);
  --fill-color-two: #223e4a;
  --fill-color-three: #ffd36b;
  --font-color: #f0f0f0;
  --font-color-dark: #9aa0b8;
  --link-color: #61dafb;
  --border-color: #657099;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--background-color);
}

.App {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: var(--font-color);
  padding: 0 50px;
  font-family: "Neucha", Times;
}

a {
  color: var(--link-color);
}

.content-div {
  background-color: var(--fill-color-two);
  width: 100%;
  margin: 15px 0;
  border-radius: 10px;
}

.border {
  border: solid 2px var(--border-color);
}

nav {
  background-color: var(--fill-color-two);
  width: 100%;
  border-radius: 0 0 10px 10px;
  padding-top: 5px;
}

nav > p, nav > h1 {
  margin: 5px 0;
}

nav > p {
  font-size: 12px;
}

.nav-button {
  display: inline-block;
  margin: 0 10px;
  height: 35px;
  text-decoration: none;
  font-size: 25px;
  border-radius: 10px;
  color: var(--font-color)
}

.nav-button > p, .nav-button > svg {
  display: inline;
  margin: 0 2px;
}

.nav-button:hover {
  background-color: var(--fill-color)
}

.nav-button > svg {
  position: relative;
  top: 3px;
}

.img-div {
  background-color: var(--fill-color);
}

img {
  border-radius: 30px;
  width: 50%;
  height: auto;
}

.text-content {
  margin: 20px 30px;
}

.title {
  margin: 30px 0 0 0;
}

#go-top {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  height: auto;
  width: 50px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: var(--fill-color-three);
  color: #bdb299;
  cursor: pointer;
  padding: 15px;
  border-radius: 50%;
}

.footer {
  padding-bottom: 5px;
  color: var(--font-color-dark);
  font-size: 15px;
}